
import Masker from 'vux/src/components/masker/index.vue'

import axios from 'axios'

export default {
data () {
return {
list: [{
title: '新建笔记',
desc: '输入文字以创建笔记',
target: 'input',
img: require('@/assets/note-add-input.jpg')
}, {
title: '新建MarkDown',
desc: '以markdown的形式创建笔记',
target: 'markdown',
img: require('@/assets/note-add-input.jpg')
}, {
title: '新建语音笔记',
desc: '随时随地记录灵感',
target: 'voice',
img: require('@/assets/note-add-input.jpg')
}, {
title: '新建图片文件',
desc: '拍照或上传图片以创建笔记',
target: 'img',
img: require('@/assets/note-add-scan.jpg')
}, {
title: '新建手写笔记',
desc: '使用画笔创建手写笔记',
target: 'scan',
img: require('@/assets/note-add-upload.png')
}]
}
},
methods: {
onClickMask (item) {
item.target === 'img'
? this.$refs.photo.dispatchEvent(new MouseEvent('click'))
: this.$router.push(`/note/add/${item.target}`)
},
saveImg (e) {
const file = e.target.files[0]
let param = new FormData() 
param.append('file', file, file.name) 
param.append('chunk', '0') 
console.log(param.get('file')) 
let config = {
headers: {'Content-Type': 'multipart/form-data'}
}

axios.post(`/api/savemedia`, param, config)
.then(res => {
console.log(res)
})
}
},
components: {
Masker
}
}



