
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import axios from 'axios'
import 'font-awesome/css/font-awesome.min.css'
export default {
components: {
Group,
Cell,
CellBox,
XHeader,
InlineCalendar
},
data () {
return {
item: {},
diffRes: []
}
},
created () {
this.item = this.$route.params.item
this.diffRes = this.item.diffRes
},
mounted () {
if (this.item.fileType === 'html') {
this.$refs.htmlContent.innerHTML = this.item.content
}
},
methods: {
editFile () {
let name = ''
switch (this.item.fileType) {
case 'md': name = 'noteAddMakdown'
break
case 'html': name = 'noteAddInput'
break
default: name = 'noteAddMakdown'
break
}
const opt = {
name,
params: {
item: this.item
}
}
this.$router.push(opt)
},
saveEver (index) {
this.diffRes[index].removed = false
this.diffRes.splice(++index, 1)
},
saveCur (index) {
this.diffRes.splice(index, 1)
this.diffRes[index].added = false
},
saveBoth (index) {
this.diffRes[index].removed = false
this.diffRes[++index].added = false
},
saveFile () {
let content = ''
let haveDiff = false
this.diffRes.forEach(el => {
if (el.removed || el.added) {
AlertModule.show({
content: '还存在冲突，请解决冲突后再提交！'
})
haveDiff = true
} else {
content = content + el.value
}
})
!haveDiff && this.save(content)
},
save (content) {

const body = {
userId: this.userId,
fileId: this.item.id,
...this.item,
content: content
}
axios
.post(`/api/savefile`, body)
.then((res) => {
res.data.status && this.$router.push({
path: '/note/list'
})
}).catch((error) => {
console.log(error)
})
}
}
}



