
import XInput from 'vux/src/components/x-input/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import vueLoading from 'vue-loading-template'
import axios from 'axios'
import { getItem } from '../../utils/storage'
export default {
components: {
XButton,
XHeader,
PopupHeader,
Popup,
Group,
Radio,
XInput,
Box,
XProgress,
vueLoading
},
data () {
return {
show: false,
isOpen: true,
duration: 0,
currentTime: 0,
recordIng: false,
item: {},
title: '',
userId: getItem('user').id,
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
opt: 0,
voiceOpt: '播放'
}
},
created () {
this.item = this.$route.params.item
},
mounted () {
},
methods: {
updateTime (e) {
this.currentTime = (e.target.currentTime / this.duration) * 100
if (this.currentTime === 100) {
this.voiceOpt = '播放'
this.currentTime = 0
}
},
ready () {
},
error () {
},
ended () {





},
showVoice () {
this.isOpen = !this.isOpen
if (this.isOpen) {
this.voiceOpt = '暂停'
this.duration = this.$refs.audio.duration
this.$refs.audio.play()
} else {
this.voiceOpt = '播放'
this.$refs.audio.pause()
}
},
saveFile () {
const body = {
userId: this.userId,
fileId: '',
title: this.title === '' ? '无标题' : this.title,
type: this.opt,
content: JSON.stringify(this.item.blob),
fileType: 'voice'
}
console.log(body)
axios
.post(`/api/savefile`, body)
.then((res) => {
res.data.status && this.$router.push({
path: '/note/list'
})
}).catch((error) => {
console.log(error)
})
}
}
}



